@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;

@include breakpoint('phone-sm') {
  .header-feature {
    position: relative;
    grid-column: 1 / -1;
    grid-row: 2 / 3;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: 100%;
    min-width: 100vw;
    margin: auto auto;
    overflow: hidden;
  }

  .slide {
    position: absolute;
    top: 0;
    grid-column: 1 / -1;
    grid-row: 1 /-1;
    min-width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows:  1fr auto 1fr;
    justify-content: center;
    align-items: center;
    align-content: center;
    overflow: hidden;
    transition: opacity 1s ease-in-out;
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -ms-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
  }
  

  .slide-theme {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr repeat(7,minmax(min-content, max-content)) 1fr;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: auto;
    row-gap: 0.75em;
    min-width: auto;
    box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.5);
    padding: 1.75rem;
    margin-left: auto;
    margin-right: auto;
    border-radius: 7px;
    z-index: 1;
  
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(111, 252, 252, 0.119); 
      backdrop-filter: blur(1px); 
      border-radius: inherit;
      z-index: -1;
    }
  }

  .shop-name {
    display: none;
    grid-row: 1 / 2;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: auto;
    text-align: center;
    min-width: 100%;
    padding-top:  1rem;
    padding-left: 2em;
    padding-right:2em ;
  
    h1 {
      font-size: f.$font-size-xl *2.25;
      font-family: f.$font-script;
      color: c.$color-alt-white;
      font-weight: f.$font-weight-light;
    }
  }

  .theme-caption {
    grid-row: 2 / 3;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;
    min-height: min-content;
    text-align: center;
    min-height: min-content;
    min-width: 100%;
  
  h2 {
    font-family: f.$font-primary;
    color: c.$color-alt-white;
    font-size: f.$font-size-normal-plus *1.25;
    letter-spacing: f.$letter-space-normal;
    font-weight: f.$font-weight-normal;
    }
  }

  .florist-name {
    grid-row: 4 / 5;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    text-align: center;
    height: auto;
    min-width: 100%;
    padding-bottom: 0;
    margin-top: 1rem;
    z-index: 1000;
      
  p {
    font-family: f.$font-primary;
    color: c.$color-alt-white;
    font-size: f.$font-size-normal-plus *1.35;
    letter-spacing: f.$letter-space-headline-sans;
    font-weight: f.$font-weight-normal;
    }
  }

  .theme-icon {
    grid-row: 5 / 6;
    display: grid;
    justify-content: center;
    align-items: center;
    align-content: center;
    height: auto;
    min-width: 100%;
    margin-top: 1.25em;
    padding-bottom: 0;
  
    img {
      height: 8rem;
      width: auto;
    }
  }

  .phone {
    position: relative;
    display: inline-flex;
    grid-row: 8 / 9;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    width: 100%;
    height: auto;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
 
    .phone-icon::before {
     content: "";
     display: inline-block;
     margin-right: 1em;
     vertical-align: middle;
   }
 
    img {
     display: inline-block;
     height: 2rem;
     width: auto;
     margin-right: 1rem
   }
 
 a {
     display: inline-block;
     list-style: none;
     text-decoration: none;
     color: c.$color-alt-white;
     font-family: f.$font-primary;
     font-size: f.$font-size-normal-plus;
    }
 }

 .email {
  position: relative;
  display: inline-flex;
  grid-row: 9 / 10;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  min-width: 100%;
  height: auto;
  margin-bottom: 2rem;

  img {
   height: 2rem;
   width: auto;
   margin-right: 1rem;
  }

 .email-icon::before {
   content: "";
   display: inline-block;
   margin-right: 1em;
   vertical-align: middle;
 }

  a {
   list-style: none;
   text-decoration: none;
   color: c.$color-alt-white;
   font-family: f.$font-primary;
   font-size: f.$font-size-normal-plus *1.2;
  }
}

.control {
  position: absolute;
  top: 0;
  width: 15vw;
  height: 100%;
  background-color: c.$color-arrow-bgGrey;
  opacity: 0.3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: opacity 0.5s linear;
  z-index: 2000;
}

.arrows-visibility {
  opacity: 0;
}

.control:hover {
  opacity: 1;
}

.left-arrow {
  left: 0%;
}

.right-arrow {
  right: 0%;
}

.arrow {
  height: 3rem;
  width: auto;
}

