.slider__btn {
  position: absolute;
  background: none;
  display: inline-block;
  top: 50%;
  border: none;
  opacity: 0.4;
  cursor: pointer;
}

.play-pause {
  cursor: pointer;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
}

.play-pause i {
  color: #fcf1f1;
  font-size: 20px;
}

}

