@use '../base/mixins' as*;

@include breakpoint(phone-sm) {
  #home {
    display: grid;
    grid-template-columns: 1fr min-content 1fr;
    justify-content: center;
    align-items: center;
    align-content: center;
 
    /* Define all rows to be min-content*/
    grid-template-rows:min-content 68em 1fr min-content;
    height: 100%;
    width: 100%;
    overflow: hidden;
   }
}



