/*From mixin file*/
/*Define variables for breakpoints*/
/*Label files bp1, bp2 for easier to follow file system*/


/*VARIABLES*/
$breakpoints: (
  phone-sm: 17.5em,           /* phone-sm min-width 17.5em[280px]*/
  phone-md: 20em,           /*phone-md min-width 20em [320px]*/
  tablet: 30em,             /*tablet min-width 30em [480px]*/
  tablet-portrait: 48em,    /*tablet-portrait min-width 48em [768px]*/
  tablet-landscape: 64em,   /*tablet-landscape min-width 64em [1024px]*/
  laptop: 73em,             /*laptop min-width 73em [1168px]*/
  desktop: 80em,           /*desktop min-width 100em [1280px]*/         
  desktop-lg: 100em,        /*desktop min-width 116em [11600px]*/
);
/*MOBILE FIRST*/
@mixin breakpoint($name) {
  @if not map-has-key($breakpoints, $name) {
    @warn "Invalid breakpoint `#{$name}`.";
  }
  @else {
    @debug "Breakpoint value: #{map-get($breakpoints, $name)}";
    @media (min-width: map-get($breakpoints, $name)) {
      @content;
    }
  }
}




  
  
  
  
  
  
  









