@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;
/*Top of Landing Page Header*/
/* MOBILE FIRST 17.5em = 280px*/


@include breakpoint('phone-sm'){
   
  .header-nav {
    display: none;
  } 

  .header-brand {
    position: relative;
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    min-height: max-content;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    justify-content: center;
    align-items: center;
    align-content: center;
  }
    .brand-container {
      position: relative;
      grid-column: 1 / -1;
      grid-row: 1 / 2;
      display: grid;
      grid-template-rows: 1.5em repeat(3, minmax(min-content,max-content)) 1.5em;
      justify-content: center;
      align-items: center;
      align-content: center;
      grid-row-gap: 0.75rem;
      height: min-content;
      min-width: 100%;
      margin-top: 0;
      margin-bottom: 0;
      padding-top: 0;
      padding-bottom: 0;
      
      
      .brand-logo {
        grid-row: 2 / 3;
        display: grid;
        justify-content: center;
        align-items: center;
        align-content: center;
    
        img {
          margin-left: auto;
          margin-right: auto;
          height: 9rem;
          width: 100%;
        }
      }
     
      .shop-title {
        grid-row: 3 / 4;
        display: grid;
        justify-content: center;
        align-items: center;
        align-content: center;
      } 
        h1 {
          font-family: f.$font-headline;
          font-size: f.$font-size-normal-plus *1.25;
          font-weight: f.$font-weight-bold;
          color: c.$color-text-black;
          letter-spacing: 5px;
    
        
        }
    
        .shop-established {
          grid-row: 4 / 5;
          margin-bottom: 1em;
          margin-left: auto;
          margin-right: auto;
    
          h3 {
          font-family: f.$font-headline;
          font-weight: f.$font-weight-bold;
          font-size: f.$font-size-normal-plus;
          color: c.$color-text-black;
          letter-spacing: 3px;
        }
      }
    }
    

}










