@use '../abstracts/colors' as c;
@use '../abstracts/fonts' as f;
@use '../base/mixins' as *;
/*HEADER NAVBAR NOT TO BE MISTAKEN FOR SIDEBAR NAV*/

/*END OF HEADER NAVBAR NOT TO BE MISTAKEN FOR SIDEBAR NAV*/
/*inside sidebar parent*/

@include breakpoint(phone-sm) {

  .sidebar {
    position: absolute;
    top: 0;
    right: 0;
  }

/*Checked HAPPY With this HAMBURGER code*/
.hamburger {
  background-color: #fc09091a;
  min-width:  5rem;
  min-height: 5rem;
  position: fixed;
  top: 2.25rem;
  right:8%;
  outline: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: min-content;
  z-index: 3050;
  transition: all 1s;
  -webkit-transition: all 1s;
  -moz-transition: all 1s;
  -ms-transition: all 1s;
  -o-transition: all 1s; 

  @include breakpoint(phone-sm){
    background-color: red;
  }
 @include breakpoint(phone-md) {
    background-color: #97fa03;
  }
  @include breakpoint(tablet) {
    background-color: #fb46b8;
  }
  @include breakpoint(tablet-portrait) {
    background-color: #faee02;
  }
  @include breakpoint(tablet-landscape) {
    background-color: #4c02fa;
  }
  @include breakpoint(laptop) {
    background-color: #02d9fa;
  }
  @include breakpoint(desktop) {
    background-color: #f82475;
  }
  @include breakpoint(desktop-lg) {
    background-color: #fa93028a;
  }
}


.change .hamburger-menu {
 background-color: transparent;
}

.line {
  width: 30px;
  height: 3px; 
  background-color: c.$color-primary-dark;
  margin: 2px 0 1px; 
  transform-origin: right;
  transition: all 0.5s;
}

.change .line-1 {
  transform: rotate(-40deg) translateY(-5px);
  background-color: c.$color-primary-dark;
  -webkit-transform: rotate(-40deg) translateY(-5px);
  -moz-transform: rotate(-40deg) translateY(-5px);
  -ms-transform: rotate(-40deg) translateY(-5px);
  -o-transform: rotate(-40deg) translateY(-5px);
}

.change .line-2 {
  opacity: 0;
}

.change .line-3 {
  transform: rotate(40deg) translateY(5px);
  background-color: c.$color-primary-dark;
  -webkit-transform: rotate(40deg) translateY(5px);
  -moz-transform: rotate(40deg) translateY(5px);
  -ms-transform: rotate(40deg) translateY(5px);
  -o-transform: rotate(40deg) translateY(5px);
}


/*Navbar Contents incl Links Position Animation and Styles*/
.change.nav-content {
  position: fixed;
  top: 0;
  right: 0;
  display: grid;
  grid-template-columns:1fr 1fr 1fr;
  grid-template-rows: 1fr repeat(7, 1fr) 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 100vh;
  max-width: 60%;
  background-color: c.$color-alt-white;
  border: solid 2px c.$color-primary-dark;
  outline: 1.2px solid c.$color-primary-dark;
  outline-offset: -8px;

  z-index: 3000;
  transform: translateX(0);
  transition: transform 0.4s ease-in-out;
  -webkit-transition: transform 0.4s ease-in-out;
  -moz-transition: transform 0.4s ease-in-out;
  -ms-transition: transform 0.4s ease-in-out;
  -o-transition: transform 0.4s ease-in-out;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
}

.nav-list {
  grid-column: 2 / 3;
  grid-row: 2 / 8;
  position: relative;
  display: grid;
  grid-auto-rows:minmax(6rem, max-content);
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  grid-row-gap: 1rem;
  padding: 1rem 1rem 1rem 1rem;
  height: 100%;
  max-width: min-content;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
}

.nav-list li {
  display: grid;
  justify-content: start;
  align-items: start;
  align-content: center;
  text-align: left;
  padding: 1rem 1rem 1rem 1rem;
  width: 100%;
}

.nav-list li a {
  color: c.$color-primary-dark;
  font-family: f.$font-headline;
  font-size: f.$font-size-normal *1.2;
  font-weight: f.$font-weight-heavy;
  letter-spacing: 3px;
  list-style: none;
  text-decoration: none;
  padding: .75rem .75rem .75rem .75rem;
}

.change .nav-list {
  opacity: 9;
  visibility: visible;
}

.nav-list .nav-item-1 {
  grid-row: 2 / 3;
}

.nav-list .nav-item-2 {
  grid-row: 3 / 4;
}

.nav-list .nav-item-3 {
  grid-row: 4 / 5;
}

.nav-list .nav-item-4 {
  grid-row: 5 / 6;
}

.nav-list .nav-item-5 {
  grid-row: 6 / 7;
}

.nav-list .nav-item-6 {
  grid-row: 7 / 8;
}

/*Animated Nav List Buttons*/
.nav-item {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
  height: 100%;
  padding: 1rem 1rem 1rem 0;
  margin: 0;
  z-index: 10400;
}

.nav-link {
  display: grid;
  list-style: none;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  font-family: f.$font-primary;
  font-weight: f.$font-weight-normal;
  font-size: f.$font-size-normal-plus;
  color: c.$color-text-black;
  padding: .8rem 0;
  transition: all .5s .3s;
}

.nav-item:hover .nav-link {
  background-color: c.$color-primary-dark;
  color: #fff;
}

.nav-link::before,
.nav-link::after {
  content: "";
  content: "";
  width: 0;
  height: .1rem;
  background-color: c.$color-primary-dark;
  display: block;
  transition: width .3s cubic-bezier(.55, 0, .98, .54), background-color .5s .3s;
}

.nav-link::after {
  justify-self: end;
}

.nav-item:first-child {
  position: relative;
}

